<template>
  <div>
    <base-table
      :columns="columns"
      :data="data"
      @detail="detail"
      rowKey="insuranceType"
      :spanMethod="merge">
    </base-table>
    <base-table
      v-show="!detailHidden"
      :columns="detailColumns"
      :data="detailData"
      rowKey="insuranceType">
    </base-table>
  </div>
</template>

<script>
const BaseTable = () => import('@/components/BaseTable')

export default {
  name: 'MonthInfo',
  components: {BaseTable},
  data () {
    return {
      detailHidden: true,
      columns: [
        {prop: 'detail', width: 120, label: '详情', align: 'center', operation: true, operations: [{text: '详情', event: 'detail'}]},
        {prop: 'insuranceType', width: 120, label: '险种', align: 'center'},
        {prop: 'premiumScale', width: 120, label: '保费规模(万元)', align: 'center'},
        {prop: 'finalPayRate', width: 120, label: '终极赔付率(%)', align: 'center'},
        {prop: 'marketCostRate', width: 120, label: '市场费用率(%)', align: 'center'},
        {prop: 'operationCostRate', width: 120, label: '经营成本率(%)', align: 'center'}
      ],
      data: [
        {
          insuranceType: '交强险合计',
          premiumScale: '225',
          finalPayRate: '65.1',
          marketCostRate: '16.8',
          operationCostRate: '98'
        },
        {
          insuranceType: '商业险合计',
          premiumScale: '225',
          finalPayRate: '65.1',
          marketCostRate: '16.8',
          operationCostRate: '98'
        },
        {
          insuranceType: '交商合计',
          premiumScale: '450',
          finalPayRate: '65.1',
          marketCostRate: '16.8',
          operationCostRate: '98'
        }
      ],
      detailColumns: [
        {prop: 'bizSector', width: 120, label: '业务板块', align: 'center'},
        {prop: 'insuranceType', width: 120, label: '险种', align: 'center'},
        {prop: 'premiumScale', width: 120, label: '保费规模(万元)', align: 'center'},
        {prop: 'finalPayRate', width: 120, label: '终极赔付率(%)', align: 'center'},
        {prop: 'marketCostRate', width: 120, label: '市场费用率(%)', align: 'center'},
        {prop: 'operationCostRate', width: 120, label: '经营成本率(%)', align: 'center'}
      ],
      detailData: [
        {
          bizSector: '家用车-新车',
          insuranceType: '交强险合计',
          premiumScale: '225',
          finalPayRate: '65.1',
          marketCostRate: '16.8',
          operationCostRate: '98'
        },
        {
          bizSector: '家用车-新车',
          insuranceType: '商业险合计',
          premiumScale: '225',
          finalPayRate: '65.1',
          marketCostRate: '16.8',
          operationCostRate: '98'
        },
        {
          bizSector: '家用车-新车',
          insuranceType: '交商合计',
          premiumScale: '450',
          finalPayRate: '65.1',
          marketCostRate: '16.8',
          operationCostRate: '98'
        },
        {
          bizSector: '家用车-旧车',
          insuranceType: '交强险合计',
          premiumScale: '225',
          finalPayRate: '65.1',
          marketCostRate: '16.8',
          operationCostRate: '98'
        },
        {
          bizSector: '家用车-旧车',
          insuranceType: '商业险合计',
          premiumScale: '225',
          finalPayRate: '65.1',
          marketCostRate: '16.8',
          operationCostRate: '98'
        },
        {
          bizSector: '家用车-旧车',
          insuranceType: '交商合计',
          premiumScale: '450',
          finalPayRate: '65.1',
          marketCostRate: '16.8',
          operationCostRate: '98'
        }
      ]
    }
  },
  methods: {
    detail () {
      this.detailHidden = !this.detailHidden
    },
    merge ({row, column, rowIndex, columnIndex}) {
      if (columnIndex === 0) {
        if (rowIndex === 0) {
          return [this.data.length, 1]
        } else {
          return [0, 0]
        }
      }
    }
  }
}
</script>

<style scoped>

</style>



